<template>
  <div class="change">
    <el-upload
      ref="upload"
      accept=".pdf"
      drag
      action="http://127.0.0.1:8013/v1/change/pdf"
      :before-remove="beforeRemove"
      :limit="1"
      :before-upload="handleBefore"
      :on-exceed="handleExceed"
      :on-remove="handleRemove"
      :on-success="handleSuccess"
      :on-error="handleError"
    >
      <i class="el-icon-upload"></i>
      <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
      <div class="el-upload__tip" slot="tip">只能上传PDF文件，且不超过10M</div>
    </el-upload>

    <el-row>
      <el-button type="primary">主要按钮 {{ msg }}</el-button>
    </el-row>
  </div>
</template>
  
<script>
export default {
  name: "Change",
  data() {
    return {
      msg: "This is login page!",
    };
  },
  methods: {
    handleBefore(files) {
      if (files.size / 1024 / 1024 > 2) {
        this.$message.warning("文件大于2M");
        return false;
      }
    },
    handleExceed(files, fileList) {
      this.$message.warning(`当前限制选择 ${files.length} 个文件`);
    },
    handleRemove(file, fileList) {
      // console.log("删除文件 ", file, fileList);
    },
    beforeRemove(file, fileList) {
      if (file && file.status === "success") {
        return this.$confirm(`确定移除 ${file.name}？`);
      }
    },
    handleSuccess(response, file, fileList) {
      if (response.code != 0) {
        this.$refs.upload.clearFiles();
        this.$message.warning(response.msg);
        return;
      }
    },
    handleError(err, file, fileList) {
      // console.log("上传文件失败 ", err, file, fileList);
    },
  },
};
</script>

<style>
.change {
  margin-top: 5%;
}
</style>



